import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid, Button, Typography } from '@material-ui/core';
import { Section } from 'components/organisms';
import { Image } from 'components/atoms';
import { SectionHeader } from 'components/molecules';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
    heroTitle: {
        color: theme.palette.primary.lgray
    },
    wrapper: {
        backgroundColor: theme.palette.primary.dgray,
        backgroundImage: 'url(/svg/polygons_bg.svg)',
        backgroundPosition: 'center center',
        backgroundSize: 'cover'
    },
    image: {
        [theme.breakpoints.down('sm')]: {
            maxWidth: 500,
        },
    },
}));

const FirstHero = props => {
    const { className, ...rest } = props;
    const classes = useStyles();

    const theme = useTheme();
    const isMd = useMediaQuery(theme.breakpoints.up('md'), {
        defaultMatches: true,
    });

    return (
        <div className={clsx(className, classes.wrapper)} {...rest}>
            <Section>
                <Grid
                    container
                    justifyContent="space-between"
                    alignItems="center"
                    spacing={4}
                    direction={isMd ? 'row' : 'column-reverse'}
                >
                    <Grid
                        item
                        xs={12}
                        md={6}
                        data-aos={'fade-up'}
                    >
                        <SectionHeader
                            title={
                                <span className={classes.heroTitle}>
                                    <Typography component="span" variant="inherit" color="primary">P2P Seller </Typography>
                                    - peer to peer logistics, fulfillment and team-sharing network
                                </span>
                            }
                            subtitle="Technology platform for e-commerce sellers"
                            ctaGroup={[
                                <Button variant="contained" color="primary" size="large">How P2P Seller works?</Button>,
                            ]}
                            align="left"
                            disableGutter
                            titleVariant="h3"
                        />
                    </Grid>
                    <Grid
                        item
                        xs={12}
                        md={6}
                    >
                        <Image
                            src="/images/phones-first.png"
                            alt="phones"
                            className={classes.image}
                            data-aos="fade-up-left"
                        />
                    </Grid>
                </Grid>
            </Section>
        </div>
    );
};

FirstHero.propTypes = {
    /**
     * External classes
     */
    className: PropTypes.string,
};

export default FirstHero;
